@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

.containerHome{
  background-image: url("../../assets/img/pcandcoffe-dark.jpg");
  height: 100%;
  /* filter: brightness(50%); */
  /* width: 100vw !important; */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  margin-left: 0 !important;
  margin-right: 0 !important;
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.parallaxImg{
  background-image: url("../../assets/img/pcandcoffe.jpg");
  height: 100vh;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  /* position: fixed; */
  margin-left: 0 !important;
  margin-right: 0 !important;
  padding-left: 0 !important;
  padding-right: 0 !important;
}


.gridHome {
  margin: auto;
  width: 100vw;
  height: 100vh;
  /* filter: opacity(1); */
  /* filter: brightness(150%); */
}

.gridHome .row{
  margin: 0 0 0 0;
  padding: 0 0 0 0;
}

.description{
  font-size: 0.4cm;
  text-align: center
}

.mainHome {
  margin: auto;
  font-family: 'Neue Haas Display Medium',sans-serif;
  line-height: 20px;
  color: white;
}

.mainHome h1{
  font-size: 2.5rem;
  animation: fadein 4s forwards;
}

.mainHome div p{
  font-size: 1rem;
  animation: fadein 10s forwards;
  text-align: center !important
}

h1 .row{
  /* margin-right: 0 !important;
   */
   width: fit-content !important;
}

.startBracket{
  margin: auto;
  color: #FF00FF;
  size: 50%;
  font-family: 'Courier New', Courier, monospace;
  text-align: right;
}

.startBracket h1{
  font-size: 9rem;
  font-weight: 500;
  text-align: right;
  animation: fadein 9s forwards;
}

.endBracket{
  margin: auto 0;
  color: #FF00FF;
  font-family: 'Courier New', Courier, monospace;
  text-align: left
}

.endBracket h1{
  text-align: left;
  font-size: 9rem;
  font-weight: 500;
  animation: fadein 9s forwards;
}





.arrow
{
  /* position: relative; */
  margin: auto;
  width: 40px;
  height: 40px;
  bottom: 0 !important;
  /**
   * Dark Arrow Down
   */
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain;  
}

.bounce {
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

@media screen and (max-width: 590px){
  /* #mainHome {
    margin: auto;
    font-size:1000px ;
    font-family: 'Neue Haas Display Medium',sans-serif;
    line-height: 20px;
  } */
  .containerHome {
    background-attachment: scroll;
  }
  
  #startbracket{
    display: none !important;
  }

  #endbracket{
    display: none !important;
  }
}